import React from 'react';
import './Board.css';
import Speech from 'react-speech';
import Image from './Image';
import Word from './Word';
import Status from './Status';
import data11 from './words/words11.json'
import data12 from './words/words12.json'
import data13 from './words/words13.json'
import sound from "./sounds/ding.mp3";

class Board extends React.Component {

    constructor(props) {
        super(props);
        console.log("---- ---- ---- ---- ---- ---- ---- ---- v1243 ");
        console.log("board constructor");
        this.handleClick01 = this.handleClick01.bind(this);
        this.handleClick02 = this.handleClick02.bind(this);
        this.handleClick03 = this.handleClick03.bind(this);
        this.handleClick04 = this.handleClick04.bind(this);
        var queryString = window.location.search;
        var urlParams = new URLSearchParams(queryString);
        this.data = data12;
        this.dataParam = urlParams.get('data');
        if (this.dataParam) {
            if (this.dataParam == "11") {
                this.data = data11;
            }
            else if (this.dataParam == "12") {
                this.data = data12;
            }
            else if (this.dataParam == "13") {
                this.data = data13;
            }
        }
        this.audio = new Audio(sound);
        this.images = [ 
            "olaf12.jpg", "olaf11.png", 
            "anaelsa.jpg", "sven12.png", 
            "isabela1.png", "isabela2.png", 
            "mirabel1.png", "poppy1.png", 
            "rosita1.png", "lol1.jpg", 
            "ariel1.png", "isabela3.jpg"
        ];
        this.imageIndex = 0;
        this.getImage();
        this.words = [ "a", "b", "c", "d" ];
        this.getWords();
        this.chosenIndex = 0;
        this.getChosenIndex();
        this.imageCorrect = "thumbsup2.png";
        this.imageNeutral = "smiley21.png";
        this.imageResult = this.imageNeutral;
        // setting state
        this.state = { 
            imageIndex: this.imageIndex,
            chosenIndex: this.chosenIndex,
            imageResult: this.imageResult
        };
    }

    render() {
      return (
        <div class="board">
            <div class="row">
                <div class="column">
                    <div class="block">
                        <Image imageName={this.images[this.state.imageIndex]} />
                    </div>
                    <div class="block" id="block01" onClick={this.handleClick01} >
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Female"
                            displayText={this.words[0]}
                            text={this.words[0]} />
                    </div>
                    <div class="block" id="block02" onClick={this.handleClick02} >
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Female"
                            displayText={this.words[1]}
                            text={this.words[1]} />
                    </div>
                </div>
                <div class="column">
                    <div class="block blockstatus">
                        <Speech 
                            textAsButton={true}
                            displayText="??"
                            text={this.words[this.chosenIndex]} />
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Male"
                            displayText="??"
                            text={this.words[this.chosenIndex]} /> 
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Female"
                            displayText="??"
                            text={this.words[this.chosenIndex]} /> 
                        <Status hint={this.words[this.chosenIndex]} imageName={this.state.imageResult} />
                    </div>
                    <div class="block" id="block03" onClick={this.handleClick03} >
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Female"
                            displayText={this.words[2]}
                            text={this.words[2]} /> 
                    </div>
                    <div class="block" id="block04" onClick={this.handleClick04} >
                        <Speech 
                            textAsButton={true}
                            voice="Google UK English Female"
                            displayText={this.words[3]}
                            text={this.words[3]} />
                    </div>
                </div>
                
            </div>
        </div>
      );
    }

    getImage() {
        console.log("getImages");
        this.imageIndex = Math.floor(Math.random() * this.images.length);
        console.log("image:", this.imageIndex, this.images[this.imageIndex]);
    }

    getWords() {
        console.log("getWords");
        // console.log(data);
        var chosen = [];
        while (chosen.length < 4) {
            const random = Math.floor(Math.random() * this.data.length);
            var newWord = this.data[random];
            if (!chosen.includes(newWord)) {
                console.log("word:", random, newWord);
                chosen.push(newWord);
            }
        }
        console.log("chosen:", chosen);
        this.words = chosen;
    }
    
    getChosenIndex() {
        this.chosenIndex = Math.floor(Math.random() * 4);
        console.log("CHOSEN chosenIndex:", this.chosenIndex, this.words[this.chosenIndex], this.chosenIndex);
    }

    redoState() {
        this.setState(state => ({
            imageIndex: this.imageIndex,
            chosenIndex: this.chosenIndex,
            imageResult: this.imageResult
        }));
    }

    handleClick01() {
        console.log("click 01");
        var wordId = 0;
        this.wordCheck(wordId);
    }
    handleClick02() {
        console.log("click 02");
        var wordId = 1;
        this.wordCheck(wordId);
    }
    handleClick03() {
        console.log("click 03");
        var wordId = 2;
        this.wordCheck(wordId);
    }
    handleClick04() {
        console.log("click 04");
        var wordId = 3;
        this.wordCheck(wordId);
    }

    wordCheck(wordId) {
        console.log("wordCheck");
        if (wordId == this.chosenIndex) {
            console.log("Yay");
            this.imageResult = this.imageCorrect;
            this.redoState();
            setTimeout(() => {
                this.audio.play();
            }, 900);
            setTimeout(() => {
                this.getImage();
                this.getWords();
                this.getChosenIndex();
                this.imageResult = this.imageNeutral;
                this.redoState();
            }, 2500);
        }
        else {
            console.log("Nay");
        }
    }

}

export default Board;
